import React from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';

import {showProductDetails} from '../actions';

const Products = (props) => {

    const [sortState, setSortState] = React.useState(false);
    const [filterCategory, setFilterCategory] = React.useState(0);

    const sortAscending = (products) => {
        products.sort((a,b) => {
            if(a.price < b.price) return -1;
            if(a.price > b.price) return 1;
            return 0;
        });
        return productListRender(products, props.category, props.showProductDetails);
    };

    
    const sortDescending = (products) => {
        products.sort((a,b) => {
            if(a.price < b.price) return 1;
            if(a.price > b.price) return -1;
            return 0;
        });
        return productListRender(products, props.category, props.showProductDetails);
    };

    //filter function before rendering
    const filteringFunction = (category) => {
        
        const deepCopyState = props.products.map(item => {
            return {...item}
        });

        if(category === 1){
            return deepCopyState.filter(ele => ele.price <= 300);
        }
        if (category === 2){
            return deepCopyState.filter(ele => ele.price > 300);
        }
        return deepCopyState;
    }

    //utility function
    const returnElement = () =>{

        const auxCopyState = filteringFunction(filterCategory);
        
        if(sortState){
            return sortAscending(auxCopyState);
        }
        return sortDescending(auxCopyState);
    }

    return (
    <div>

        <p>
            <button type="button" onClick= {() => setSortState(true)}>Show low price to high</button>
            <button type="button" onClick= {() => setSortState(false)}>Show high price to low</button>
        </p>
        <p>
            <button type="button" onClick = {() => setFilterCategory(1)}>Filter products less than or equal to Rs 300</button>
            <button type="button" onClick = {() => setFilterCategory(2)}>Filter products more than Rs 300</button>
            <button type="button" onClick = {() => setFilterCategory(0)}>No Filter</button>
        </p>
        <div className="ui cards">{returnElement()}</div>
        
        <Link to="/ecommerce/showCart/"><button type="button">Go To Cart</button></Link>
    </div>
    );
}

const mapStateToProps = (state) => {

    if(state.showCategory){    
        localStorage.setItem("showCategory", state.showCategory);
    }
    return {
        products: state.products,
        category: state.showCategory || parseInt(localStorage.getItem("showCategory"))
    }
};

export default connect(mapStateToProps,{showProductDetails})(Products);



const productListRender = (products, category, showProductDetails) => 
    {
        return products.map(product => {
        if(product.category === category){
            return (
                <div key={product.product_id} className="card">
                        <div className="content">
                            <div className="header">
                                Title: {product.product_title}
                            </div>
                            <div className="description">
                                Price: Rs {product.price}
                            </div>
                            <div className="extra content">
                                <div className="ui two buttons">
                                    <Link to="/ecommerce/product/detail">
                                        <button className="ui basic green button"
                                        onClick={() => showProductDetails(product.product_id)}
                                        >Show Details
                                        </button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
            return <div key={product.product_id}></div>;
        });
    }