import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

let i = 0;

// component defined here
const Checkout = (props) => {
    let totalPrice = 0;
    if(props.cart.length>0){
        return (
        <div>
            <h1>Cart</h1>
            {
                props.cart.map(product => {
                    return (
                        totalPrice += product.price,
                        <div key={++i}>
                            <p>Product Title: {product.product_title} Price: {product.price}</p>
                        </div>
                    );
                })
            }
            <h3>Total Price: Rs {totalPrice}</h3>
            <Link to="/ecommerce"><button type="button" onClick={() => alert("Order placed!!!")}>Buy</button></Link>
        </div>
        );
    }
    return <div>Nothing bought yet. GO SHOPPING!!!</div>;

};

const mapStateToProps = (state) => {
    
    console.log();
    return {
        cart: state.cart || JSON.parse(localStorage.getItem("cart")),
    }
}
export default connect(mapStateToProps)(Checkout);