import { combineReducers } from 'redux';

const productsReducer = () => {
    return [
        {product_id: 1, category: 1, product_title: "mango", detail: "good for health", price: 200},
        {product_id: 2, category: 2, product_title: "banana", detail: "good for fitness", price: 300},
        {product_id: 3, category: 1, product_title: "guava", detail: "good for mental health", price: 400},
        {product_id: 4, category: 2, product_title: "watermelon", detail: "good for pysical fitness", price: 500},
        {product_id: 5, category: 3, product_title: "potato", detail: "nutritious", price: 100},
        {product_id: 6, category: 3, product_title: "lady finger", detail: "gives protein", price: 200}
    ]
};

const showProductReducer = (showProduct=null, action) => {
    if (action.type === "SHOW_PRODUCT_DETAILS"){
        return action.payload;
    }
    return showProduct;
}

const showCategoryReducer = (showCategory = null, action) => {
    if (action.type === "SHOW_CATEGORY_LIST"){
        return action.payload;
    }
    return showCategory;
}


const cartReducer = (cart = [], action) => {
    let auxCart = [...cart];

    if(action.type === "ADD_TO_CART"){
        auxCart = [...auxCart, action.payload];

    }else if(action.type === "REMOVE_FROM_CART"){
        const removeProduct = action.payload;
        auxCart = [];
        let flag = false;

        for(let i=0; i<cart.length; i++){
            if(cart[i].product_id === removeProduct.product_id && !flag){
                flag = true;
            }else{
                auxCart.push(cart[i]);
            }
        }

    }
    
    localStorage.setItem("cart", JSON.stringify(auxCart));

    return auxCart;
}

export default combineReducers({
    products: productsReducer,
    showProduct: showProductReducer,
    showCategory: showCategoryReducer,
    cart: cartReducer,
})