import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {addToCart} from '../actions';

const ProductDetail = (props) => {
    if(props.focusedProduct){
        const focusedProduct = props.products.filter(product => product.product_id === props.focusedProduct)[0];
        return (
        <div className="ui items">
            <div className="ui item">
                <div className="content">
                    <div className="header">            
                       <h1>Product Details:</h1>
                    </div>
                    <div className="description">
                        <p>Product Title: {focusedProduct.product_title}</p>
                        <p>Product Description: {focusedProduct.detail}</p>
                        <p>Product id: {focusedProduct.product_id}</p>
                        <p>Product Category: {focusedProduct.category}</p>
                        <p className="ui basic red">Price: {focusedProduct.price}</p>
                    </div>
                    
                    <div className="extra content">
                        <div className="ui one button">
                            <button className="ui basic primary button" onClick={() => props.addToCart(focusedProduct)}>Add To Cart</button>
                        </div>
                    </div>
                </div>
            </div>
            
            <Link to="/ecommerce/showCart/"><button type="button">Go To Cart</button></Link>
        </div>
        );
    }
    return <div></div>;
};

const mapStateToProps = (state) => {
    if(state.showProduct){    
        localStorage.setItem("showProduct", state.showProduct);
    }

    return {
        products: state.products,
        focusedProduct: state.showProduct || parseInt(localStorage.getItem("showProduct"))
    }
}

export default connect(mapStateToProps, {addToCart})(ProductDetail);