import React from 'react';

import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {removeFromCart} from '../actions';

let i = 0;

// component defined here
const ShowCart = (props) => {
    if(props.cart.length>0){
        return (
        <div>
            <h1>Cart</h1>
            {
                props.cart.map(product => {
                    return (
                        <div key={++i}>
                            <p>Product Title: {product.product_title} Price: {product.price}</p>
                            <button onClick={() => props.removeFromCart(product)}>Remove</button>
                        </div>
                    );
                })
            }
            <Link to="/ecommerce/showCart/checkout/"><button type="button">Checkout</button></Link>
        </div>
        );
    }
    return <div>Cart is empty. GO SHOPPING!!!</div>;

};

const mapStateToProps = (state) => {
    
    console.log();
    return {
        cart: state.cart || JSON.parse(localStorage.getItem("cart")),
    }
}
export default connect(mapStateToProps, {removeFromCart})(ShowCart);