// Action creator selected category
export const showProductDetails = (product) => {
    return {
        type: "SHOW_PRODUCT_DETAILS",
        payload: product
    }
};

// Action creator selected product
export const showCategory = (category) => {
    return {
        type: "SHOW_CATEGORY_LIST",
        payload: category
    }
};

//Action creator add to cart
export const addToCart = (product) => {
    return {
        type: "ADD_TO_CART",
        payload: product
    }
}

//Remove from cart
export const removeFromCart = (product) => {
    return {
        type: "REMOVE_FROM_CART",
        payload: product
    }
}