import React from 'react';

import { connect } from 'react-redux';

import { showCategory } from '../actions';


import {Link} from 'react-router-dom';
 

class CategoryList extends React.Component{

    renderCategory = () => {
        
        const categories_id = [];
        //getting categories id from all products
        this.props.categories.forEach(product => {
            if(!(categories_id.includes(product.category))){
                categories_id.push(product.category);
            }
        });
        
        return categories_id.map(ele => {
            return (
                    <div className="card" key={ele}>
                        <div className="content">
                            <div className="header">                        
                                {"Category: "+ ele}
                            </div>
                            <div className="extra content">
                                <div className="ui two buttons">
                                    <Link to="/ecommerce/product">
                                        <button className="ui basic green button"
                                        onClick={() => this.props.showCategory(ele)}
                                        >
                                        Show
                                        </button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                );
        });

    };
    
    render(){
        return (
            <div>
                <div className="ui cards">
                {
                    this.renderCategory()
                }
                </div>
                <Link to="/ecommerce/showCart/"><button type="button">Go To Cart</button></Link>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        categories: state.products,
    }
};

export default connect(mapStateToProps, {showCategory})(CategoryList);