import React from 'react';


import {Router,Route} from 'react-router-dom';
import history from '../history';

import CategoryList from './CategoryList';
import Checkout from './Checkout';
import Product from './Product';
import ProductDetail from './ProductDetail';
import ShowCart from './ShowCart';

const App = () => {
    return (
    <div>
        <Router history={history}>
            <Route path = "/ecommerce/" exact component={CategoryList} />
            <Route path = "/ecommerce/product" exact component={Product} />
            <Route path = "/ecommerce/product/detail" exact component={ProductDetail} />
            <Route path = "/ecommerce/showCart/" exact component={ShowCart} />
            <Route path = "/ecommerce/showCart/checkout/" exact component={Checkout} />
        </Router>
    </div>
    );
};


export default App;